@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;0,700;1,700&display=swap";
:root {
  --color-white: #fff;
  --color-light-white: #ffffffb2;
  --color-black: #111;
  --color-dark-purple: #1e1823;
  --color-orange: #fd9222;
  --color-light-orange: #fcf1e0;
  --white-color: #fff;
  --snow-color: #fafafa;
  --linen-color: #fcf1e0;
  --carrot-orange-color: #fd9222;
  --eerie-black-color: #1e1823;
  --smoky-black-color: #111;
  --smoky-transparent-black-color: #1111111a;
  --font-montserrat: "Montserrat", sans-serif;
  --description-font-size: 14px;
  --title-font-size: 24px;
  --mobile-title-font-size: 20px;
  --sub-title-font-size: 18px;
  --pop-up-font-size: 10px;
  --logo-weight: 700;
  --bold-weight: 600;
  --medium-weight: 500;
  --light-weight: 400;
  --mobile-breakpoint: 375px;
  --tablet-breakpoint: 768px;
  --desktop-breakpoint: 1200px;
  --animation-duration: .3s;
}

* {
  box-sizing: border-box;
  font-family: var(--font-montserrat);
  scroll-behavior: smooth;
  outline: 0;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body, h1, h2, h3, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

nav ul, ul, .list {
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

html {
  background: #fafafa;
  min-width: 375px;
}

.container {
  width: 375px;
  margin: 0 auto;
}

.section-space {
  margin-bottom: 72px;
}

@media screen and (width >= 768px) {
  .container {
    width: 768px;
    margin: 0 auto;
  }

  .section-space {
    margin-bottom: 100px;
  }
}

@media screen and (width >= 1200px) {
  .container {
    width: 1200px;
    margin: 0 auto;
  }

  .section-space {
    margin-bottom: 128px;
  }
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.visually-hidden {
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-nav-container {
