  letter-spacing: -.54px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 700;
  line-height: 1.1;
}

.header-modal-button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 20px;
  right: 20px;
}

.js-close-menu {
  stroke: var(--color-white);
}

.header-modal-button:focus, .header-modal-button:hover {
  stroke: var(--color-orange);
}

.x-button {
  width: 28px;
  height: 28px;
  transition: all .25s ease-in-out;
}

.x-button:hover, .x-button:focus {
