  height: 40px;
}

.header-nav-mobile {
  margin-top: 136px;
  margin-bottom: auto;
  margin-left: 50px;
}

.nav-mobile {
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  display: inline-flex;
}

.nav-mobile .link {
  color: var(--color-white);
  font-weight: 500;
  line-height: 1.14;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.nav-mobile .link:focus, .nav-mobile .link:hover {
  color: var(--color-orange);
}

.sm-svg-mobile {
  stroke: var(--color-white);
  fill: #0000;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.sm-svg-mobile:focus, .sm-svg-mobile:hover {
  stroke: var(--color-orange);
}

.header-sm-links, .sm-mobile {
  gap: 18px;
  display: flex;
}

.js-open-menu {
  background-color: #0000;
  border: none;
}

.js-menu-container {
  background-color: var(--color-dark-purple);
  z-index: 999;
  flex-direction: column;
  width: 220px;
  height: 812px;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.js-menu-container.is-open {
  transform: translateX(0);
}

