    height: 24px;
  }
}

@media screen and (width <= 1199px) {
  .header-nav-links, .header-sm-container {
    display: none;
  }
}

@media screen and (width >= 1200px) {
  .js-open-menu, .mobile-menu {
    display: none;
  }

  .header-container {
    padding-top: 24px;
    padding-bottom: 32px;
  }

  .header-logo {
    letter-spacing: -.54px;
    margin-right: 188px;
    font-size: 18px;
    line-height: 1.11;
  }

  .header-logo-svg {
    width: 24px;
    height: 24px;
  }

  .header-nav-links {
    gap: 24px;
    display: flex;
  }

  .header-nav-links .link {
    color: var(--color-black);
